import React from "react"

interface VertexProps {
  x1: number
  y1: number
  x2: number
  y2: number
}

const RADIUS = 7

const Vertex: React.FC<VertexProps> = ({ x1, y1, x2, y2 }) => {
  let length = Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2))
  let dir: [number, number] = [(x2 - x1) / length, (y2 - y1) / length]
  let from = `${x1 + dir[0] * RADIUS},${y1 + dir[1] * RADIUS}`
  let to = `${x2},${y2}`
  return (
    <svg>
      <circle
        key={`${x1}${y1}`}
        cx={x1}
        cy={y1}
        r={`${RADIUS}`}
        stroke="red"
        strokeWidth="3"
        fill="none"
      />
      <circle
        key={`${x1}${y1}'`}
        cx={x1}
        cy={y1}
        r={`${RADIUS}`}
        stroke="transparent"
        strokeWidth="10"
        fill="transparent"
        onClick={() => {}}
      />
      <polyline
        key={`${x2}${y2}`}
        points={`${from} ${to}`}
        stroke="red"
        strokeWidth="3"
        fill="none"
      />
      <polyline
        key={`${x2}${y2}'`}
        points={`${from} ${to}`}
        stroke="transparent"
        strokeWidth="10"
        fill="transparent"
        onClick={() => {}}
      />
    </svg>
  )
}

export default Vertex
