import { Streamlit } from "streamlit-component-lib"
import { useRenderData } from "streamlit-component-lib-react-hooks"
import image from "./assets/ground.png"
import React, { useState, useEffect } from "react"
import Vertex from "./Vertex"
import { clearScreenDown } from "readline"

const LinePlacementPlot: React.VFC = () => {
  // "useRenderData" returns the renderData passed from Python.
  const renderData = useRenderData()
  const [coordinates, setCoordinates] = useState<
    [number, number, number, number][]
  >([])
  const [vertices, setVertices] = useState<JSX.Element[]>([])
  const [returnVals, setReturnVals] = useState<
    [number, number, number, number][]
  >([])

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if ((event.ctrlKey || event.metaKey) && event.key === "z") {
        setCoordinates((coords) => coords.slice(0, -1))
      }
    }

    window.addEventListener("keydown", handleKeyDown)
  }, [])

  useEffect(() => {
    const renderArgs = () => {
      const args: [number, number, number, number][] = renderData.args["arg"]
      setCoordinates((coords) => [...coords, ...args])
      setReturnVals((vals) => [...vals, ...args])
    }
    renderArgs()
  }, [renderData.args])

  useEffect(() => {
    const updateVertices = () => {
      const verticesArray: JSX.Element[] = []
      for (let i = 0; i < coordinates.length; i++) {
        const coord = coordinates[i]
        verticesArray.push(
          <Vertex x1={coord[0]} y1={coord[1]} x2={coord[2]} y2={coord[3]} />
        )
      }
      setVertices(verticesArray)
    }
    updateVertices()
  }, [coordinates])

  let firstClick: [number, number] | null = null

  // Arguments that are passed to the plugin in Python are accessible
  // via `renderData.args`.
  // Ex: const name = renderData.args["name"]

  // Return values using Streamlit.setComponentValue(newValue)

  // Streamlit sends us a theme object via renderData that we can use to ensure
  // that our component has visuals that match the active theme in a
  // streamlit app.
  const theme = renderData.theme
  const style: React.CSSProperties = {}

  // Maintain compatibility with older versions of Streamlit that don't send
  // a theme object.
  if (theme) {
    // Use the theme object to style our button border. Alternatively, the
    // theme style is defined in CSS vars.
    const borderStyling = `1px solid gray`
    style.border = borderStyling
    style.outline = borderStyling
  }

  const getClickCoords = (event: {
    target: any
    clientX: number
    clientY: number
  }) => {
    var e = event.target
    var dim = e.getBoundingClientRect()
    var x = event.clientX - dim.left
    var y = event.clientY - dim.top
    return [x, y]
  }

  const click = (event: any) => {
    const [x, y] = getClickCoords(event)
    if (firstClick === null) {
      firstClick = [x, y]
    } else {
      const [x1, y1] = firstClick
      const nextCoord: [number, number, number, number] = [x1, y1, x, y]
      Streamlit.setComponentValue([...returnVals, nextCoord])
      setCoordinates((coords) => [...coords, nextCoord])
      setReturnVals((vals) => [...vals, nextCoord])
      firstClick = null
    }
  }

  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        display: "flex",
        flexDirection: "row",
      }}
    >
      <div
        style={{
          flexDirection: "column",
        }}
      >
        {coordinates.map((coords, index) => (
          <TextField
            setCoordinates={setCoordinates}
            coords={coords}
            index={index}
          />
        ))}
      </div>
      <div
        style={{
          width: 540,
          height: 600,
          backgroundImage: `url(${image})`,
          position: "relative",
          display: "inline-block",
        }}
        onClick={(event) => {
          click(event)
        }}
      >
        <svg width={540} height={600}>
          {vertices}
        </svg>
      </div>
    </div>
  )
}

interface TextFieldProps {
  coords: [number, number, number, number]
  index: number
  setCoordinates: React.Dispatch<
    React.SetStateAction<[number, number, number, number][]>
  >
}

const TextField: React.FC<TextFieldProps> = ({
  coords,
  index,
  setCoordinates,
}) => {
  return (
    <div>
      <TextInput
        placeholder={"First X-coordinate"}
        value={String(coords[0])}
        index={index}
        whichCoord={0}
        setCoordinates={setCoordinates}
      />
      <TextInput
        placeholder={"First Y-coordinate"}
        value={String(coords[1])}
        index={index}
        whichCoord={1}
        setCoordinates={setCoordinates}
      />
      <TextInput
        placeholder={"Second X-coordinate"}
        value={String(coords[2])}
        index={index}
        whichCoord={2}
        setCoordinates={setCoordinates}
      />
      <TextInput
        placeholder={"Second Y-coordinate"}
        value={String(coords[3])}
        index={index}
        whichCoord={3}
        setCoordinates={setCoordinates}
      />
    </div>
  )
}

interface TextInputProps {
  placeholder: string
  value: string
  index: number
  whichCoord: number
  setCoordinates: React.Dispatch<
    React.SetStateAction<[number, number, number, number][]>
  >
}

const TextInput: React.FC<TextInputProps> = ({
  placeholder,
  value,
  index,
  whichCoord,
  setCoordinates,
}) => {
  const [focus, setFocus] = useState<boolean>(false)
  const [val, setVal] = useState<string>(value)
  return (
    <input
      type="text"
      placeholder={placeholder}
      value={val}
      style={{
        backgroundColor: "#282434",
        outline: focus ? "2px solid #ff3030" : "none",
        padding: "10px",
        color: "#ffffff",
        borderRadius: "5px",
        border: "none",
        fontSize: "12px",
        margin: "4px",
        boxSizing: "border-box",
      }}
      onChange={(e) => {
        setVal(e.target.value)
        if (!isNaN(+e.target.value)) {
          const newCoord = +e.target.value
          console.log(newCoord)

          setCoordinates((coords) => {
            let coordRef = coords[index]
            coordRef[whichCoord] = newCoord
            coords[index] = coordRef
            return [...coords]
          })
        }
      }}
      onFocus={() => setFocus(true)}
      onBlur={() => setFocus(false)}
    />
  )
}

export default LinePlacementPlot
